@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap);
body {
  margin: 0;
  font-family:'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins';
}

.titulo {
  display: flex;
  justify-content: center;
}
.CartWidget{font-size:35px;margin:.5rem .3rem 0}.hidden{visibility:hidden;pointer-events:none}.cantidad{display:flex;flex-direction:column;justify-content:center;align-items:center}
.navbar{display:flex;background-color:#b9e2fd}.ul-navbar{display:flex;margin-right:1rem}.ul-navbar a{text-decoration:none;color:#000}.ul-navbar li{list-style-type:none;margin:1rem;font-size:1.3rem}.ul-navbar a:hover{color:#b33a3a}.header-navbar{margin:1rem}.logo{height:6rem}
.card-item{display:flex;flex-direction:column}.img-cart{width:400px;height:400px;margin-bottom:1rem}.titulo-cart{margin-top:1rem}
.boton-volver-cart{margin-left:2rem}.card-cart{display:flex;flex-direction:column;align-items:center}.card-cart p{text-align:center}.boton-vaciar{width:140px;margin-bottom:1rem}.boton-terminar{width:200px;margin-bottom:1rem}.contenedor-cart{margin-bottom:2rem}
.form-checkout{display:flex;flex-direction:column;width:400px}.div-checkout{display:flex;flex-direction:column;align-items:center;justify-content:center}.boton-enviar{margin-top:1rem}.contenedor{display:flex;flex-direction:column;align-items:center;justify-content:center}
.item-count{display:flex;flex-direction:column;align-items:center}
.item-detail-img{width:400px;height:400px;object-fit:cover}.card-detail{height:400px;width:400px}.contenedor-item-detail{display:flex;flex-direction:column;align-items:center}.contenedor-item-detail h2{margin-top:1rem}.botones-volver{display:flex;grid-gap:1rem;gap:1rem;margin-bottom:1rem}.terminar-compra{display:flex;align-items:center;margin:1rem}
.loader{width:100vw;height:100vh;position:fixed;top:0;left:0;display:flex;justify-content:center;align-items:center}
.container{display:flex;justify-content:center}
.card-item{display:flex;align-items:center;justify-content:center}.img-card{object-fit:contain;width:100%;height:100%}.card-body{display:flex;flex-direction:column;align-items:center}
.contenedor{padding:5rem 0 0 3rem}.grid-items{display:grid;grid-template-columns:repeat(3, 1fr);padding:2rem 0 2rem 0}
